<script>
import axios from 'axios'
export default {
  name: 'Room06',
data () {
        return {  
        room:{},  
        roomNo:'',
        price:'',
        roomStatus: '',
        roomType:'',
        username: '',
        };
    },
    async mounted(){
      await axios.get('http://localhost:3000/getRoom/626e448c184cd400fbf72f78')
      .then((res) => {
        console.log(res.data);
        this.roomNo = res.data.result.roomNo;
        this.price = res.data.result.price;
        this.roomStatus = res.data.result.roomStatus;
        this.roomType = res.data.result.roomType;
      })
      .catch((error) => {
        console.log(error)
      });
    },
    methods: {
        onlogOut() {
        localStorage.removeItem('dataUser')
        this.$router.push({ name: 'Home' })
        }
        
    }
}
</script>

