<template>
  <div class="middle">
    <div class="txt">
    <form @submit.prevent="updateRoom()">
        <h1></h1>
        <br><br>
        <h3> {{room.roomNo}}  {{room.roomType}}
            <br>
            <br>
             1  1   6  
            <br>
             2  
            <br>
             1   1   2   1   1 
            <br>
            <br><br>
            <input
            v-model="room.roomStatus"
            type="text"
            class="input"
            placeholder=""
            required />
            
           <button type="submit" class="button">Submit</button>
        </h3>
    </form>
    </div>
</div>

</template>

<script>
import axios from 'axios'
export default {
  name: 'Croom10',
data () {
        return {    
        room:{},
        username: '',
        };
    },
    async mounted(){
      await axios.get('http://localhost:3000/getRoom/626e44ab184cd400fbf72f80')
      .then((res) => {
        console.log(res.data);
        this.room = res.data.result
      })
      .catch((error) => {
        console.log(error)
      });
    },
    create(){
        axios.get('http://localhost:3000/edit-room/626e44ab184cd400fbf72f80')
        .then((res) => {
            this.room = res.data.result
        })
    },
    methods: {
        updateRoom(){
            axios.put('http://localhost:3000/update-room/626e44ab184cd400fbf72f80',this.room)
            .then((res) => {
                console.log(res)
                this.$router.push( '/room04' )
            }).catch(error =>{
                console.log(error)
            })
        },
        onlogOut() {
        localStorage.removeItem('dataUser')
        this.$router.push({ name: 'Home' })
        }
        
    }
}
</script>

<style scoped>
.txt{
  color: #462419;
  font-size: 20px;
}
.middle {
  
  top: 50%;
  left: 50%;
  margin: auto;

}
input[type="text"],
input[type="password"] {
  width: 50%;
  padding: 15px;
  margin: 5px 0 22px 0;
  display: inline-block;
  border: none;
  background: #f1f1f1;
}

input[type="text"]:focus,
input[type="password"]:focus {
  background-color: #ddd;
  outline: none;
}
</style>